<template>
  <div>
    <table border="1">
      <tr>
        <td>글번호</td>
        <td>제목</td>
        <td>작성자</td>
        <td>작성일</td>
      </tr>
      <tr v-for="(b, index) in searchlist" :key="`${index}_boardlist.bList`">
        <td v-text="b.bnum"></td>
        <td>
          <router-link :to="`/board/read?bnum=${b.bnum}`">{{ b.btitle }}</router-link>
        </td>
        <td v-text="b.bwriter"></td>
        <td v-text="getFormatDate(b.bregdate)"></td>
      </tr>
    </table>
    <router-link to="/board">목록</router-link>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import moment from "moment";
export default {
  computed: {
    ...mapGetters(["searchlist"]),
  },
  data() {
    return {
      payload: {
        key: "",
        word: "",
      },
    };
  },
  created() {
    this.payload.key = this.$route.query.key;
    this.payload.word = this.$route.query.word;
    // let payload = { key: key, word: word };
    this.$store.dispatch("getSearchList", this.payload);
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format("YY.MM.DD HH:mm:ss");
    },
  },
};
</script>

