<template>
  <div>
    <h3>결과 : {{ msg }}</h3>
    <router-link to="/board">목록보기</router-link>
  </div>
</template>
<script>
export default {
  data() {
    return {
      msg: "",
      // bnum:(Type:Number),
    };
  },
  created() {
    let bnum = this.$route.query.bnum;
    this.$store.dispatch("deleteBoard", {bnum}).then((resp) => {
      this.msg = resp.data;
    });
  },
};
</script>
