import Vue from "vue";
import Vuex from "vuex";
import http from "@/util/http-common";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    boardlist: [],
    board: [],
    searchlist: [],
  },
  mutations: {
    setBoardList(state, payload) {
      state.boardlist = payload;
    },
    setBoard(state, payload) {
      state.board = payload;
    },
    setSearchList(state, payload) {
      state.searchlist = payload;
    },
  },
  actions: {
    getBoardList(context) {
      http.get("/board/list").then((resp) => {
        context.commit("setBoardList", resp.data);
      });
      // http.get("/boardMain").then((resp) => {
      //   context.commit("setBoardList", resp.data["bList"]);
      // });
    },
    getBoard(context, payload) {
      // console.log(payload.bnum);
      http.get("/board/" + payload.bnum).then((resp) => {
        context.commit("setBoard", resp.data);
      });
    },
    postBoard(context, payload) {
      return http.post("/board", payload);
    },
    updateBoard(context, payload) {
      console.log(payload);
      return http.put("/board", payload);
    },
    deleteBoard(context, payload) {
      console.log(payload.bnum);
      return http.delete("/board/" + payload.bnum);
    },
    getSearchList(context, payload) {
      console.log(payload.key + " " + payload.word);
      return http
        .get("/board/search", {
          params: {
            key: payload.key,
            word: payload.word,
          },
        })
        .then((resp) => {
          console.log(resp);
          context.commit("setSearchList", resp.data);
        });
    },
  },
  getters: {
    boardlist(state) {
      return state.boardlist;
    },
    board(state) {
      return state.board;
    },
    searchlist(state) {
      return state.searchlist;
    },
  },
});
