<template>
  <div>
    <form>
      <select v-model="search.key">
        <option value="btitle">제목</option>
        <option value="bnum">글 번호</option>
        <option value="bwriter">작성자</option>
      </select>
      <input type="text" v-model="search.word" />
      <button @click.prevent="searchSubmit">검색</button>
    </form>
  </div>
</template>

<script>
export default {
  name: "search",
  data() {
    return {
      search: {
        key: "",
        word: "",
      },
    };
  },
  methods: {
    searchSubmit() {
      this.$router.push(`/board/search?key=${this.search.key}&word=${this.search.word}`);
      //   this.$store.dispath("getSearchList", this.search);
    },
  },
};
</script>
